import * as yup from 'yup'

const suggestionSchema = yup.object().shape({
  text: yup
    .string()
    .required('Veillez donner une description de vnement')
    .min(20, 'au moins 20 caractres')
    .max(10000, '10000 caractres maximum'),
  title: yup
    .string()
    .required('Veillez indiquer un titre')
    .min(5, 'au moins 5 caractres')
    .max(50, '50 caractes mximum'),
})

export default suggestionSchema
